<template>
  <div class="news">
    <!-- logo -->
    <div class="pz_top hidden-xs">
      <div class="top">
          <div class="logo">
            <img src="../assets/logo.png">
          </div>
      </div>
    </div>
    <div class="btnBox">
      <!--  -->
      <div class="navBtn" @mouseover="showNav">
          <img src="../assets/navBtn.png" alt="">
        </div>
    </div>
    <!--  -->
    <div class="topBanner">
      <img src="../assets/newsBg.png" alt="">
    </div>
    <!--  -->
    <div class="pageTitle">
      <p class="chinTitle"></p>
      <p class="engTitle">NEWS</p>
    </div>
    <!--  -->
    <div class="cateBtn">
      <ul>
        <li 
          v-for="(item,index) in newsCate" 
          :key="index"
          @click="changeNewsCate(item,index)"
          :class="{activeNav:activeNavIndex==index}"
          >{{item}}</li>
      </ul>
    </div>
    <!--  -->
    <div class="newsList">
      <ul>
        <li v-for="(item,index) in currList" :key="index">
          <div class="leftPic">
            <img :src="item.imgUrl" alt="">
          </div>
          <div class="rightTitle">
            <p class="p1">
              {{item.title}}
            </p>
            <p class="p2">
              {{item.detail[0][0].duanluo[0].p}}
            </p>
            <p class="p3">
              {{item.month}} / 
              <span>
              {{item.year}}
              </span>
            </p>
          </div>
          <div class="moreBtn" @click="toDetail(item,index)">
            MORE&nbsp;&nbsp;>
          </div>
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="page">
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="pageChange"
        :page-size="4"
        :total="total">
      </el-pagination>
    </div>
    <!--  -->
    <page-bottom></page-bottom>
    <!--  -->
    <div class="navPage" :class="isShowNav? 'navPageShow' : ''">
      <div class="closed" @click="closeNav">
        <div class="logo"><a href="/" title=""><img src="../assets/logo.png" alt="" title=""></a></div>
        <div class="pf_menu_btn"><img src="../assets/close.jpg" alt=""></div>
      </div>
      <div id="navSlide" class="ph_menu">
              <div class='nav_box'>
                  <li class="nli l1" id="nav_1">
                      <p><span @click="toHome"><a href="javascrip:viod(0)" target="_self" class="l1_a" title=" "> <i>Home</i></a></span></p>
                  </li>
                  <li class="nli l2" id="nav_2">
                      <p><span @click="toCasePage"><a href="javascrip:viod(0)" target="_self" class="l1_a" title=" "> <i>Case</i></a></span>
                      </p>
                  </li>
                  <li class="nli l3" id="nav_3">
                      <p><span @click="toService"><a href="javascrip:viod(0)" target="_self" class="l1_a" title=" "> <i>Service</i></a></span></p>
                  </li>
                  <li class="nli l5" id="nav_5">
                      <p><span @click="toCurpartner"><a href="javascrip:viod(0)" target="_self" class="l1_a" title=" "> <i>Customer</i></a></span>
                      </p>
                  </li>
                  <li class="nli l6" id="nav_6">
                      <p><span @click="toNews"><a href="javascrip:viod(0)" target="_self" class="l1_a" title=" "> <i>News</i></a></span></p>
                  </li>
                  <li class="nli l4" id="nav_4">
                      <p><span @click="toAbout"><a href="javascrip:viod(0)" target="_self" class="l1_a" title=" "> <i>About</i></a></span></p>
                  </li>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import PageBottom from '../components/footer/PageBottom.vue'
export default {
  name: 'News',
  components: {
    PageBottom
  },
  data() {
		return{
      isShowNav: false,
      newsCate: ['','',''],
      activeNavIndex: 0,
      detail: '',
      current: 1,
      pageSize: 4,
      total: null,
      allList: [],
      companyNewsList:[],
      hangYeNewsList:[],
      newsList: [],
      currList: [], //
		}
  },
  created() {
    this.allList = [
      {
        imgUrl: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/1.jpg',
        title: '',
        detail: [
          [
            { 
              duanluo: [
                {
                  p: '5G5G+5G ',
                },
                {
                  p: '',
                },
                {
                  p: '.',
                },
                {
                  p: 'AIAI .',
                },
                {
                  p: '',
                },
                {
                  p: 'AR',
                }
              ],
              image: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/1.jpg'
            }
          ],
          [
            {
              duanluo: [
                {
                  p: '+5G',
                }
              ],
              image: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/3.jpg'
            }
          ],
          [
            {
              duanluo: [
                {
                  p: '5G',
                  p: '.'
                }
              ],
              image: ''
            }
          ]
        ],
        month: '02-17',
        year: '2019'
      }, //

      {
        imgUrl: require('../assets/news3-2.jpg'), //tu
        title: '',
        detail: [
          [
            {
              duanluo: [
                {
                  p: '.',
                }
              ],
              image: require('../assets/news3-2.jpg') //tu
            }
          ],
          [
            {
              duanluo: [
                {
                  p: '',
                },
                {
                  p: '	',
                },
                {
                  p: '	',
                },
                {
                  p: '	',
                },
                {
                  p: '	.',
                },
                {
                  p: '	'
                }
              ],
              image: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/4.jpg'
            }
          ],
          [
            {
              duanluo: [
                {
                p: '',
                },
                {
                p: '	.',
                },
                {
                p: '	',
                },
                {
                p: '	AI.',
                },
                {
                p: '	AIAIAIAIAI',
                },
                {
                p: '	AIAIAIAIAI',
                }
              ],
              image: require('../assets/news2-3.jpg')//tu
            }
          ],
          [
            {
              duanluo: [
                {
                  p: '',
                }
              ],
              image: ''//tu
            }
          ]

        ],
        month: '04-15',
        year: '2019'
      },  //

      {
        imgUrl: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/6.jpg', 
        title: 'AI',
        detail: [
          [
            {
              duanluo: [
                {
                  p: '',
                }
              ],
              image: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/6.jpg' //tu
            }
          ],
          [
            {
              duanluo: [
                {
                  p: 'AIAIArtificial Intelligence ',
                },
                {
                  p: 'BIMAIAI30AI',
                },
                {
                  p: '24AIAI24AI.',
                },
                {
                  p: 'AIAIAI'
                }
              ],
              image: 'https://baogongxia1.oss-cn-shenzhen.aliyuncs.com/%E6%A1%88%E4%BE%8B/img/7.gif'
            }
          ],
          [
            {
              duanluo: [
                {
                  p: 'AI'
                }
              ],
              image: ''
            }
          ],
        ],
        month: '06-10',
        year: '2019'
      }  //
    ]

    this.newsList = this.allList
    this.hangYeNewsList = this.allList
    // 
      this.currList = this.newsList.slice(0, 4)
      this.total = this.newsList.length
  },
  methods: {
    // 
    toDetail(item,index) {
      this.$router.push({
        path: '/newsDetail',
        name: 'NewsDetail',
        params: {
          data: item
        }
      })
      sessionStorage.setItem("news", JSON.stringify(item));
    },
    // 
    pageChange(pageNumber){
      this.current = pageNumber;  // 
      // 
      const start = (this.current - 1) * this.pageSize;
      const end = start + this.pageSize;
      // 
      this.currList = this.newsList.slice(start, end)
      window.scroll(0,515)
    },
    // 
    changeNewsCate(item,index) {
      this.activeNavIndex = index
      // 
      if(item == ''){
        this.newsList = this.allList
      }
      if(item == ''){
        console.log(this.companyNewsList)
        this.newsList = this.companyNewsList
      }
      if(item == ''){
        this.newsList = this.hangYeNewsList
      }
      this.currList = this.newsList.slice(0, 4)
      this.total = this.newsList.length
    },

		// 
		showNav(){
			this.isShowNav = true
		},
		// 
		closeNav(){
			this.isShowNav = false
    },
    toHome(){
			this.$router.push({
				path: '/',
				name: 'Home'
			})
		},
		toCasePage(){
			this.$router.push({
				path: '/case',
				name: 'Case'
			})
		},
		toService(){
			this.$router.push({
				path: '/service',
				name: 'ServicePage'
			})
		},
		toCurpartner(){
			this.$router.push({
				path: '/curpartner',
				name: 'Curpartner'
			})
		},
		toNews(){
			this.$router.push({
				path: '/news',
				name: 'News'
			})
		},
		toAbout(){
			this.$router.push({
				path: '/about',
				name: 'About'
			})
		}
	}
}
