<style lang="scss" scoped>
.news{
  width: 100%;
  position: relative;
  .pz_top{
			float: left;
			width: 100%;
			position: absolute;
			left: 0;
			top: 45px;
			z-index: 80;
			-webkit-transition: all 0.3s ease-out 0s;
			-moz-transition: all 0.3s ease-out 0s;
			-o-transition: all 0.3s ease-out 0s;
			transition: all 0.3s ease-out 0s;
			.top{
				width: 100%;
				float: left;
				height: 26px;
				padding: 0 60px;
				.logo {
					float: left;
					width: 204px;
					margin-top: 0px;
					img{
						width: 100%;
    				float: left;
					}
				}
			}
		}
  .btnBox{
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    height: 1.86rem;
    z-index: 99;
  }
  .topBanner{
    width: 100%;
    height: auto;
    margin-bottom: 1.02rem;
    img{
      width: 100%;
      height: auto;
    }
  }
  .cateBtn{
    width: 100%;
    height: 1.02rem;
    ul{
      text-align: center;
      height: 100%;
      margin: 0 auto;
      li{
        display: inline-block;
        vertical-align: middle;
        height: 100%;
        line-height: 1.02rem;
        height: 1.02rem;
        font-size: 0.16rem;
        padding: 0 0.5rem;
        color: #c1c1c1;
        cursor: pointer;
        background: url('../assets/cateBg.png') no-repeat right center;
      }
      .activeNav{
        color: #2a1e32;
      }
      // li:hover{  //
      //   color: #2a1e32;
      // }
      li:nth-last-child(1){
        background: none;
      }
    }
  }
  .newsList{
    width: 88.90%;
    height: auto;
    margin: 0 auto;
    margin-top: 0.7rem;
    ul{
      width: 100%;
      height: auto;
      li{
        width: 100%;
        height: 2.7rem;
        border-bottom: 2px solid #eee;
        margin-bottom: 0.4rem;
        position: relative;
        .leftPic{
          width: 29.87%;
          height: 100%;
          float: left;
          img{
            width: 4.6rem;
            height: 1.75rem;
          }
        }
        .rightTitle{
          width: 70%;
          height: 100%;
          float: left;
          .p1{
            width: 52%;
            height: 0.58rem;
            font-size: .24rem;
            color: #757670;
            // line-height: 0.58rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          .p2{
            font-size: 0.18rem;
            width: 64.27%;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            line-height: 0.42rem;
            color: #716e75;
          }
          .p3{
            line-height: 1.18rem;
            font-size: 0.2rem;
            color: #cac8cb;
            margin-top: 0.55rem;
            span{
              font-size: 0.14rem;
            }
          }
        }
        .moreBtn{
          width: 2.15rem;
          height: 0.6rem;
          cursor: pointer;
          line-height: 0.6rem;
          text-align: center;
          font-size: 0.18rem;
          color: #b0afb4;
          position: absolute;
          top: 0.76rem;
          right: 0;
          border-left: 1px solid #b0afb4;
        }
        .moreBtn:hover{
          color: #120000;
        }
      }
      li:last-child{
        border: none;
      }
    }
  }

  .navPage{
    position: fixed;
    width: 100%;
    height: 100%;
    left: -100%;
    top: 0;
    display: block !important;
    float: left;
    z-index: 101;
    background: rgba(0,0,0,0.8);
    -webkit-transition: all 0.3s ease-out 0s;
    -moz-transition: all 0.3s ease-out 0s;
    -o-transition: all 0.3s ease-out 0s;
    transition: all 0.3s ease-out 0s;
    .closed{
			width: 100%;
			height: 120px;
			float: left;
			padding: 0.4rem 0.4rem 0 0.6rem;
			.logo{
				float: left;
    		margin-top: 7px;
				width: 2.04rem;
				img{
					width: 100%;
    			float: left;
				}
			}
			.pf_menu_btn{
				float: right;
				cursor: pointer;
				width: 40px;
				margin-right: 30px;
				img{
					width: 100%;
					float: left;
					-webkit-transition: all 0.3s ease-out 0s;
					-moz-transition: all 0.3s ease-out 0s;
					-o-transition: all 0.3s ease-out 0s;
					transition: all 0.3s ease-out 0s;
				}
			}
			.pf_menu_btn:hover{
				img{
					transform: rotate(180deg);
				}
			}
		}
		.ph_menu {
			position: absolute;
			left: 0;
			top: 120px;
			height: 100%;
			width: 100%;
			text-align: left;
			overflow: hidden;
			border-top: solid 1px #353536;
			.nav_box {
				width: 100%;
				position: relative;
				height: 100%;
				.nli {
					float: left;
					position: relative;
					height: 100%;
					width: 16.666%;
					border-right: solid 1px #353536;
					p{
						span{
							float: left;
							width: 100%;
							padding-top: 56px;
							text-align: center;
							-webkit-transition: all 0.3s ease-out 0s;
							-moz-transition: all 0.3s ease-out 0s;
							-o-transition: all 0.3s ease-out 0s;
							transition: all 0.3s ease-out 0s;
							.l1_a {
								float: left;
								width: 100%;
								color: #fff;
								font-size: 24px;
								line-height: 32px;
								padding: 0 10px;
								position: relative;
								i{
									display: block;
									width: 100%;
									font-size: 14px;
									color: #aeaeae;
									line-height: 18px;
									text-transform: uppercase;
									font-style: normal;
								}
							}
						}
					}
				}
				.nli:hover{
					.l1_a {
						color: #337ab7;
						font-weight: bold;
						i{
							font-weight: normal;
						}
					}
					span {
						padding-top: 78px;
					}
				}
			}
		}
  }
  .page{
    width: 100%;
    height: 1.14rem;
    display: block;
    .el-pagination {
      white-space: nowrap;
      padding: 2px 5px;
      color: #fff;
      font-weight: 700;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .el-pager{
        .active{
          background-color: #3f3f3f;
        }
      }
    }
  }
  .navPageShow{
		left: 0;
	}
}
</style>
<style>
.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #3f3f3f;
  color: #FFF;
}
@media screen and (max-width: 1360px){
  .news .newsList ul li{
    height: 2.2rem !important;
}
  .news .newsList ul li .rightTitle{
    width: 65% !important;
    height: 100%;
    float: right !important;
  }
  .news .newsList ul li .leftPic img{
    height: auto;
  }
  .news .newsList ul li .rightTitle .p3[data-v-6aa13012] {
    line-height: 0.2rem !important;
    margin-top: 0.6rem !important;
  }
}
</style>



