					.l1_a {
						color: #337ab7;
						font-weight: bold;
						i{
							font-weight: normal;
						}
					}
					span {
						padding-top: 78px;
					}
				}
			}
		}
  }
  .page{
    width: 100%;
    height: 1.14rem;
    display: block;
    .el-pagination {
      white-space: nowrap;
      padding: 2px 5px;
      color: #fff;
      font-weight: 700;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .el-pager{
        .active{
          background-color: #3f3f3f;
        }
      }
    }
  }
  .navPageShow{
		left: 0;
	}
}
</style>
<style>
@media screen and (max-width: 1360px){
  .service .wrap1{
    height: 6.25rem !important;
  }
  .service .wrap1 .item{
    height: auto;
  }
  .service .wrap1 .item .img{
    width: 1.43rem !important;
    height: 1.43rem !important;
    margin: 0.55rem auto !important;
}
.service .wrap2 .content{
  width: 75.41% !important;
}
.service .wrap2 .content .serItem .icon{
    /* left: 28% !important; */
}
.service .wrap2 .content .serItem:hover .titleBox span{
    left: 13% !important;
}
.service .wrap2 .content .serItem:hover .titleBox span::before{
    width: 30% !important;
    right: -20% !important;
}
.service .wrap2 .content .serItem:hover .titleBox span::after{
    width: 30% !important;
    left: -20% !important;
}
.service .wrap2 .title .t1 span::before{
  width: 33% !important;
}
.service .wrap2 .title .t1 span::after{
  width: 33% !important;
}
}
</style>




